html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  height: 100vh;
  background-color: #af7732;
  display: grid;
  grid-template-columns: 1.5fr 1.9fr 1.5fr;
  grid-template-rows: 1fr 5fr 1fr;
  gap: 1px;
}
body span {
  background-color: #fff;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  text-transform: uppercase;
}
body .menu {
  grid-column-start: -2;
}
body .center-image {
  grid-column: 2/3;
  grid-row: 2/3;
  background-image: url("https://wallpapers.com/images/hd/4k-ultra-hd-galaxy-eye-wlqwkvwci6w5hx28.jpg");
  background-size: cover;
  background-position: center;
}
body .number-row {
  background-color: #af7732;
  display: grid;
  grid-template-columns: 1fr;
  gap: 1px;
}
body .number-row .number {
  background-color: #fff;
  display: grid;
  justify-content: center;
  text-align: center;
  align-items: center;
  gap: 1px;
  cursor: pointer;
}
body .number-row .number:hover {
  background-color: #af7732;
  color: white;
}
body .scrolling-text {
  background-color: #fff;
  color: #af7732;
  white-space: nowrap;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .scrolling-text span {
  animation: scrollText 3s linear infinite forwards;
}

@keyframes scrollText {
  from {
    transform: translateX(100px);
  }
  to {
    transform: translateX(0px);
  }
}